.head {
  position: fixed;
  width: 100% !important;
  padding-top: 1rem;
  padding-bottom: 1.2rem;
  padding-left: 2.2rem;
  padding-right: 1rem;
  z-index: 150;
  background: linear-gradient(180deg, #746969, rgba(82, 77, 77, 0.5));
}

.topbar-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  min-height: 66px;
}
.index-logo { 
  width: 4rem;
  height: 4rem;
}
.logoDesc {
  font-size: 1.5rem;
  font-weight: bold;
}
.num-2 {
  color: rgb(43, 204, 142) !important;
  font-size: x-large;
  font-family: monospace;
}
.social-container {
}
.IDO-Pre {
  background-color: rgb(38, 99, 80) !important;
}
.IDO-Pre:hover {
  background-color: rgb(96, 219, 204) !important;
}

.baseChart {
  min-width: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: antiquewhite;
}
.baseChart:hover {
  color: rgb(107, 74, 92);
}

