@media only screen and (max-width: 920px) {

  .index-logo { 
    width: 3.5rem;
    height: 3.5rem;
  }
  .logoDesc {
    font-size: 1rem;
  }

}

@media only screen and (max-width: 550px) { 

  .head {
    padding-left: 1rem;
  }

  .language { 
    padding-right: 0;
  }
  .language-btn { 
    padding: 0.2rem;
  }
  .index-logo { 
    width: 2.5rem;
    height: 2.5rem;
  }
  .index-IDO { 
    width: 100%;
  }
  .index-IDOBtn { 
    padding: 0.2rem;
    width: 100%;
  }

}
